@import url(https://fonts.googleapis.com/css?family=Cardo|Fira+Code&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav{background-color:white;width:100vw;height:68px;z-index:1;position:fixed;top:0;left:0;border-top:0.5px solid black;border-bottom:0.5px solid black;display:flex;justify-content:space-between;align-items:center}nav a{font-size:20px;margin:0 35px;font-style:unset}nav #nav-logo{margin-left:10px;text-decoration:none;font-size:45px;font-style:italic;color:black;text-shadow:1px 1px 0 #000;-webkit-text-fill-color:white;-webkit-text-stroke-width:0.5px;-webkit-text-stroke-color:black}nav #nav-logo:hover{text-shadow:1px 1px 0 black, 2px 2px 0 #8254EB, 3px 3px 0 white, 4px 4px 0 black;transition:.1s}nav .nav-links{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:flex-start;align-items:center}nav .nav-links a{text-decoration:none}@media only screen and (max-width: 1200px){nav #nav-logo{font-size:1.4em;margin:0;margin-left:5px}nav .nav-links a{margin:0 5px;font-size:.7em}}

@font-face{font-family:"le-murmure";src:url(/static/media/le-murmure.ff900880.otf) format("opentype")}.intro-section{display:flex;flex-direction:row;align-items:center;justify-content:center;margin:0 auto;margin-top:7%;width:60%}.intro-section img{border-radius:50%;-webkit-filter:grayscale(1);filter:grayscale(1);padding:.5%;border:1px solid black}.intro-section .intro-keyinfo{margin-left:40px;display:flex;flex-direction:column}.intro-section .intro-keyinfo h1{-webkit-font-smoothing:antialiased;font-family:"le-murmure", serif;text-shadow:2px 2px 0 #000;padding:0;margin:0;font-size:7em;font-weight:normal;-webkit-text-fill-color:white;-webkit-text-stroke-width:0.5px;-webkit-text-stroke-color:black}.intro-section .intro-keyinfo p{width:80%;font-size:1.4em;margin-left:15px;text-align:justify;-webkit-hyphens:auto;-ms-hyphens:auto;hyphens:auto}@media only screen and (max-width: 1200px){.intro-section{margin-top:100px;border:none;width:100%;flex-direction:column}.intro-section .intro-keyinfo{margin:0 auto}.intro-section .intro-keyinfo h1{font-size:5em;margin:0 auto}.intro-section .intro-keyinfo p{margin:0 auto;width:95%;font-size:1.2em}}

.main-section{border:1px solid black;width:55%;margin:0 auto;margin-top:5%;padding:1%}.main-section .section-container{text-align:center;margin-bottom:4%}.main-section .section-container h2{text-shadow:2px 2px 0 #000;padding:0;margin:0;font-size:5em;font-weight:normal;-webkit-text-fill-color:white;-webkit-text-stroke-width:0.5px;-webkit-text-stroke-color:black}.main-section .section-container img{max-width:100%}.main-section .section-container h3{font-size:2em;font-weight:normal}.main-section .section-container h3 a{text-decoration:none}.main-section .section-container h4{font-size:1.5em;font-weight:200}.main-section .section-container p{font-size:1.2em;margin:1% 0}.main-section .section-container .dates{font-style:italic}.main-section .job-container,.main-section .project-container{margin:3% auto;max-width:80%}.main-section .tech-section{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;margin:.5% auto;max-width:60%}.main-section .tech-section .tech-title{font-size:.9em;padding:0 7px;font-style:italic}@media only screen and (max-width: 1200px){.main-section{border:none;border-top:1px solid black;width:96%}.main-section .section-container h2{font-size:4em}.main-section .section-container h3{font-size:1.7em}.main-section .section-container h4{font-size:1.3em}.main-section .section-container p{font-size:1.1em}}

*{margin:0;padding:0;text-decoration:none}body{font-family:"Cardo", serif}body a{color:black}a{font-style:italic}a:hover{text-shadow:1px 1px 0 #8254eb, 2px 2px 0 white, 3px 3px 0 black;transition:0.1s}footer{border-top:1px solid black;margin-top:2%;text-align:center;padding:5px 0;width:100%}

